import React from "react";
import "./styles.css";
import data from "./data.json";

export default function App() {
  const [searchVal, setSearchVal] = React.useState("");

  return (
    <React.Fragment>
      <div className="App">
        <h1>Hello to medscare</h1>
        <h2>Search...</h2>
        <input
          placeholder="Search"
          onChange={(event) => {
            setSearchVal(event.target.value);
          }}
        ></input>
        <button>Search medicine</button>
        <br />
        {`You Searched For : ${searchVal}`}
      </div>
      <br />
      <hr />
      {searchVal === ""
        ? data.map((data) => {
            return (
              <div>
                {data.sno}
                {". "}
                {data.composition}
                <br />
                Usage: {data.usage}
                <hr />
              </div>
            );
          })
        : data.map((data) => {
            let temp = new String(data.composition);
            if (temp.toLowerCase().includes(`${searchVal}`.toLowerCase())) {
              return (
                <div>
                  {data.sno}
                  {". "}
                  {data.composition}
                  <br />
                  Usage: {data.usage}
                  <hr />
                </div>
              );
            }
          })}
    </React.Fragment>
  );
}
